// src/store/index.ts
import { create } from "zustand";

type State = {
  isAuthenticated: boolean;
  userRole: string;
  login: () => void;
  logout: () => void;
};

export const useAuthStore = create<State>((set) => ({
  isAuthenticated: false,
  userRole: "",
  login: () => set({ isAuthenticated: true, userRole: "admin" }),
  logout: () => set({ isAuthenticated: false, userRole: "" }),
}));
