// src/pages/LoginPage.tsx
import React from "react";
import { Button, Form, Input, Card } from "antd";
import { useAuthStore } from "../../store";
import { useNavigate } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { isLogin } from "../../services/api";

export const Login: React.FC = () => {
  const login = useAuthStore((state) => state.login);
  let navigate = useNavigate();
  const { data, isLoading, refetch } = useQuery({
    queryKey: ["login"],
    queryFn: isLogin,
    enabled: false,
  });

  console.log(
    "🚀 ~ file: LoginPage.tsx ~ line 19",
     data
  );
  const onFinish = async (values: any) => {
    try {
      // Call your login API
      const { status, data } = await refetch();

      console.log(
        "🚀 ~ file: LoginPage.tsx ~ line 41 ~ onFinish ~ status",
        status, data
      );
      // If successful, update the auth state

      status === "success" && login();
      navigate("/dashboard");
    } catch (error) {
      console.error(error);
    }
  };

  console.log("LOADING", isLoading);

  return (
    <Form onFinish={onFinish}>
      <Card style={{ width: 300 , textAlign:'center', margin: 'auto'}}>
        <h1>Trust 2 Connect</h1>
        <Form.Item
          name="username"
          rules={[{ required: true, message: "Please input your username!" }]}
        >
          <Input placeholder="Username" />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[{ required: true, message: "Please input your password!" }]}
        >
          <Input.Password placeholder="Password" />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Log in
          </Button>
        </Form.Item>
      </Card>
    </Form>
  );
};
