import axios from "axios";
const REACT_APP_API_URL_V1 = process.env.REACT_APP_API_URL_V1
const fetchUsers = async () => {
  const { data } = await axios.get(`${REACT_APP_API_URL_V1}/users/`, 
);
  return data.data;
};

